@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600;700&display=swap);
* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-shadow: border-box;
}

:root {
  --bg: #abd1c6;
  --bg-dark: #004643;
  --bg-headline: #fffffe;
  --bg-paragraph: #133a2f;
  --bg-buttonTest: #001e1d;
  --bg-button: #f9bc60;
}

html,
body {
  background-color: #004643;
  background-color: var(--bg-dark);
}

a {
  text-decoration: none;
}

a {
  color: black;
}

.heading {
  margin: 20px 0;
}

.heading h1 {
  color: var(--bg-headline);
}

.heading p {
  color: var(--bg);
}

footer p {
  color: var(--bg);
  margin: 20px;
}

.main {
  text-align: center;
}

.main h1 {
  font-size: 20px;
  text-transform: uppercase;
}

.container {
  width: 1000px;
  margin: 0 auto;
}

.item_container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  justify-items: center;
}

.item {
  width: 100%;
  height: 100px;
  box-shadow: 2px 2px 2px black;
  background-color: whitesmoke;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.disabled {
  background-color: rgb(104, 104, 104) !important;
  width: 100%;
  height: 100px;
  box-shadow: 2px 2px 2px black;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.item:hover,
.project_item:hover {
  cursor: pointer;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.item a {
  width: 60%;
}

/* PROJECT CONTAINER */

.projectContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  justify-items: center;
}

.project_item {
  width: 100%;
  height: 150px;
  box-shadow: 2px 2px 2px black;
  background-color: whitesmoke;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: var(--bg-dark);
  text-transform: uppercase;
}

.middle {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

@media only screen and (max-width: 1024px) {
  .container {
    width: 90%;
  }

  .item_container {
    grid-template-columns: repeat(2, 1fr);
  }

  .item {
    width: 100%;
  }

  .projectContainer {
    grid-template-columns: repeat(1, 1fr);
  }

  .project_item {
    height: 100px;
  }

  img {
    width: 90% !important;
  }
}

/* BUTTON */

button {
  border: none;
  padding: 10px 20px;
  color: var(--bg-dark);
  background-color: var(--bg);
  margin: 10px;
}

button:focus {
  outline: none;
}

button:active {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

img {
  width: 30rem;
  border-radius: 10px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.801);
  margin: 20px;
}

.classcomponent {
  text-align: center;
}

.state {
  text-align: center;
  color: var(--bg-headline);
}

.state p {
  margin: 20px;
}

.props_box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.props_box p {
  color: var(--bg-headline);
}

.props_box button {
  background-color: var(--bg-button);
}



form {
  padding: 30px;
  background-color: var(--bg-buttonTest);
  border-radius: 5px;
}

form input {
  padding: 10px;
  width: 340px;
  background-color: rgb(231, 231, 231);
  border: none;
  border-radius: 5px;
}

.listAndKey {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.listAndKey li {
  text-decoration: none;
  list-style: none;
  color: var(--bg-headline);
  margin: 20px;
}

.data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.data_each {
  background-color: var(--bg-headline);
  margin: 10px;
  border-radius: 10px;
}

